


























































import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {}
})
export default class DrawCanvas extends Vue {
  public dCanvas :any = document.createElement("canvas");
  public x = 0;
  public y = 0;
  public brushColor = "#000000";
  public brushSize = 1;
  public isDrawing = false;

  public mounted(): void {
    const d:any = document.getElementById("drawCanvas");
    this.dCanvas = d!.getContext("2d");
  }
  public clearCanvas() {
    const ctx = this.dCanvas;
    const d:any = document.getElementById("drawCanvas");
    ctx!.clearRect(0, 0, d!.width, d!.height);
  }
  public mouseCoordinates(e: MouseEvent) {
    this.x = e.offsetX;
    this.y = e.offsetY;
  }
  public drawLine(x1: number, y1: number, x2: number, y2: number) {
    const ctx = this.dCanvas;
    ctx.beginPath();
    ctx.strokeStyle = this.brushColor;
    ctx.lineWidth = this.brushSize;
    ctx.lineCap = "round";
    ctx.moveTo(x1, y1);
    ctx.lineTo(x2, y2);
    ctx.stroke();
    ctx.closePath();
  }

  public draw(e: MouseEvent) {
    if (this.isDrawing) {
      this.drawLine(this.x, this.y, e.offsetX, e.offsetY);
      this.x = e.offsetX;
      this.y = e.offsetY;
    }
  }

  public beginDrawing(e: MouseEvent) {
    this.x = e.offsetX;
    this.y = e.offsetY;
    this.isDrawing = true;
  }

  public stopDrawing(e: MouseEvent) {
    if (this.isDrawing) {
      this.drawLine(this.x, this.y, e.offsetX, e.offsetY);
      this.x = 0;
      this.y = 0;
      this.isDrawing = false;
    }
  }
}
